import { Component, OnInit } from '@angular/core';
import { OnboardingService } from '../onboarding.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CdkDragDrop, moveItemInArray, transferArrayItem } from '@angular/cdk/drag-drop';
import { MatChipInputEvent } from '@angular/material/chips';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'lib-onboarding-form',
  templateUrl: './onboarding-form.component.html',
  styleUrls: ['./onboarding-form.component.scss']
})
export class OnboardingFormComponent implements OnInit {
  onboardingForm: FormGroup;
  currentUser: any;
  templates: any;
  contacts: any;
  categories: any;
  isSelected = false;
  currentOnboarding: any;
  id = "0";
  constructor(public service: OnboardingService, private fb: FormBuilder, private route: ActivatedRoute) { }

  async ngOnInit() {
    this.onCreateDynamicForm();
    await this.service.getContacts();
    await this.service.getAllProfil();
    this.service._contacts.subscribe((contacts) => this.contacts = contacts);
    this.service._templates.subscribe((data) => this.templates = data);
    this.id = this.route.snapshot.paramMap.get("id");
    if (this.id !== "0") {
      this.currentOnboarding = await this.service.getOnboarding(this.id);
      // this.onboardingForm.patchValue(this.currentOnboarding);
    }
    console.log(this.templates);

  }

  onSelectContactTemplate() {
    this.isSelected = true;
  }

  onCreateDynamicForm() {
    this.onboardingForm = this.fb.group({
      contactId: ['', Validators.required],
      templateId: [0, Validators.required]
    });
  }

  onPopulateValue() {
    const user = this.categories[0].forms;
    const populateContact = user.map((u) => ({
      ...u,
      value: this.currentUser[u.key]
    }));
    this.categories[0].forms = populateContact;
  }

  onSelect(id) {
    this.categories = this.templates.find((t) => t._id === id).categories;
    if (this.contacts) {
      this.onPopulateValue();
    }
  }

  onSelectUser(Id) {
    this.currentUser = this.contacts.find((u) => u.Id === Id);
    if (this.categories) {
      this.onPopulateValue();
    }
  }





  onAddNewCategory(e) {
    e.preventDefault();
    this.categories.push({
      name: "",
      editable: true,
      deleteable: true,
      expandMore: true,
      suffixEndpoint: "",
      forms: [
        {
          label: "",
          value: null,
          size: 80,
          isMandatory: true,
          type: "text",
          deleteable: true,
          editable: true
        },
      ],
    });
  }

  onAddNewField(...params) {
    const length = params.length;
    const forms =
      length === 1
        ? this.categories[params[0]].forms
        : length === 2
          ? this.categories[params[0]].forms[params[1]].forms
          : this.categories[params[0]].forms[params[1]].forms[params[2]].forms;
    forms.push({
      label: "",
      value: null,
      size: 80,
      isMandatory: true,
      type: "text",
      deleteable: true,
      editable: true,
      subLevel: params.length
    });
  }

  onAddNewSubLevelField(...params) {
    console.log(params.length - 1);

    const length = params.length;
    const forms =
      length === 2
        ? this.categories[params[0]].forms[params[length - 1]]
        : length === 3
          ? this.categories[params[0]].forms[params[1]].forms[params[length - 1]]
          : this.categories[params[0]].forms[params[1]].forms[params[2]].forms[
          params[length - 1]
          ];
    forms.expandMore = true;
    forms.forms = [];
    forms.forms.push({
      label: "",
      value: null,
      size: 80,
      isMandatory: true,
      type: "text",
      deleteable: true,
      editable: true,
      subLevel: params.length,
      expandMore: true
    });
    console.log(this.categories);
  }

  onRemoveField(...params) {
    const length = params.length;
    const forms =
      length === 2
        ? this.categories[params[0]].forms
        : length === 3
          ? this.categories[params[0]].forms[params[1]].forms
          : this.categories[params[0]].forms[params[1]].forms[params[2]].forms;
    forms.splice(params[length - 1], 1);
  }

  onRemoveCategory(e, i) {
    e.preventDefault();
    this.categories.splice(i, 1);
  }

  onChangeCategory() {
    return this.categories.every(
      (cat) =>
        cat.name !== "" &&
        cat.forms.every((field) => field.label !== "" && field.type !== "")
    );
  }



  onChangeType(type: string, ...params) {
    const length = params.length;
    const forms =
      length === 2
        ? this.categories[params[0]].forms
        : length === 3
          ? this.categories[params[0]].forms[params[1]].forms
          : this.categories[params[0]].forms[params[1]].forms[params[2]].forms;
    if (type === "select" || type === "radio") {
      forms[params[length - 1]].value = [];
    } else if (type === "text") {
      forms[params[length - 1]].value = "";
    } else {
      forms[params[length - 1]].value = false;
    }
  }

  onKeyPress(event: any, ...params) {
    const length = params.length;
    const forms =
      length === 2
        ? this.categories[params[0]].forms[params[length - 1]]
        : length === 3
          ? this.categories[params[0]].forms[params[1]].forms[params[length - 1]]
          : this.categories[params[0]].forms[params[1]].forms[params[2]].forms[
          params[length - 1]
          ];
    if (event.key === ";") {
      const exist = forms.value.find(
        (opt) =>
          opt.toLocaleLowerCase() ===
          event.target.value.split(";")[0].toLocaleLowerCase()
      );
      if (!exist) {
        forms.value.push(event.target.value.split(";")[0]);
        event.target.value = "";
      } else {
        event.target.value = "";
      }
    }
  }

  removeKeyword(keyword: string, ...params) {
    const length = params.length;
    const forms =
      length === 2
        ? this.categories[params[0]].forms[params[length - 1]]
        : length === 3
          ? this.categories[params[0]].forms[params[1]].forms[params[length - 1]]
          : this.categories[params[0]].forms[params[1]].forms[params[2]].forms[
          params[length - 1]
          ];
    forms.value = forms.value.filter((option) => option !== keyword);
  }

  onExpendMore(event, ...params) {
    if (event) {
      event.preventDefault();
    }
    console.log(event);

    const length = params.length;
    const forms =
      length === 1
        ? this.categories[params[0]] :
        length === 2 ? this.categories[params[0]].forms[params[length - 1]] :
          length === 3 ? this.categories[params[0]].forms[params[1]].forms[params[length - 1]] :
            this.categories[params[0]].forms[params[1]].forms[params[2]].forms[params[length - 1]];
    forms.expandMore = !forms.expandMore;
  }

  addKeywordFromInput(event: MatChipInputEvent, ...params) {
    const input = event.input;
    const value = event.value;
    const length = params.length;
    const forms =
      length === 2
        ? this.categories[params[0]].forms[params[length - 1]]
        : length === 3
          ? this.categories[params[0]].forms[params[1]].forms[params[length - 1]]
          : this.categories[params[0]].forms[params[1]].forms[params[2]].forms[
          params[length - 1]
          ];
    if (value) {
      const exist = forms.value.find(
        (opt) => opt.toLocaleLowerCase() === event.value.toLocaleLowerCase()
      );
      if (!exist) {
        forms.value.push(value.trim());
        input.value = "";
      } else {
        input.value = "";
      }
    }
  }

  drop(event: CdkDragDrop<string[]>) {
    console.log("log", this.categories);
    if (event.previousContainer === event.container) {
      moveItemInArray(
        event.container.data,
        event.previousIndex,
        event.currentIndex
      );
    } else {
      transferArrayItem(
        event.previousContainer.data,
        event.container.data,
        event.previousIndex,
        event.currentIndex
      );
    }
  }

  ingoreDefaultAction(e) {
    e.preventDefault();
  }

}
